import React from 'react'
import './css/Overgrips.css'
import Card from './Card'
import wpro from '../img/grips/w-confort.jpg'
import wcom from '../img/grips/w-comfort-negro.jpg'
import tourna from '../img/grips/tourna.jpg'
import solinco from '../img/grips/solinco.jpg'
import gripBabolat from '../img/grips/gripbabolat.jpg'
import gripTourna from '../img/grips/tournagrip.jpg'


const Overgrips = () => {
    return (
        <div className="overgrips" id='overgrips'>
            <Card 
                img={wpro}
                name={'Wilson Pro'}
                precio={'900 $'}
            />
            <Card 
                img={wcom}
                name={'Wilson confort'}
                precio={'600 $'}
            />
            <Card 
                img={tourna}
                name={'Tourna mega'}
                precio={'800 $'}
            />
            <Card 
                img={solinco}
                name={'Solinco'}
                precio={'750 $'}
            />
            <Card 
                img={gripBabolat}
                name={'Grip Babolat'}
                precio={'1500 $'}
            />
            <Card 
                img={gripTourna}
                name={'Grip Tourna'}
                precio={'1900 $'}
            />
        </div>
    )
}

export default Overgrips