import React from 'react'
import './css/Card.css'

const Card = ({img, name, precio}) => {
    
    return (
        <div className="card">
            <img className="c-img" src={img} alt="" />
            <span className="c-name">{name}</span>
            <span className="c-precio">{precio}</span>

        </div>
    )
}

export default Card