import React from 'react'
import './css/Pelotas.css'
import Card from './Card.jsx'
import penns from '../img/Pelotas/sello-negro.jpg'
import head from '../img/Pelotas/head-tour.png'
import propen from '../img/Pelotas/Dunlop-AO-Pelotas.jpg'
import wilsonrg from '../img/Pelotas/wilsonrg.jpg'
import madma from '../img/Pelotas/madma.jpg'



const Pelotas = () => {

    return (
        
        <div className="pelotas" id='pelotas'>
            <Card 
                img={penns}
                name={'Penn tournament x1'}
                precio={'600 $'}
            />
            <Card 
                img={head}
                name={'Head tour x3'}
                precio={'2900 $'}
            />
            <Card 
                img={propen}
                name={'Dunlop AO x3'}
                precio={'2900 $'}
            />
            <Card 
                img={wilsonrg}
                name={'Wilson RG x3'}
                precio={'2900 $'}
            />
            <Card 
                img={madma}
                name={'Madma padel x3'}
                precio={'2300 $'}
            />
        </div>
    )
}

export default Pelotas