import React from 'react'
import './css/Accesorios.css';
import Card from './Card';
import diadora from '../img/accesorios/diadora.jpg'
import plomo from '../img/accesorios/plomo.jpg'
import protectorpadel from '../img/accesorios/protectorpadel.jpg'
import antipelota from '../img/accesorios/anti-pelota.jpg'
import antihead from '../img/accesorios/anti-head.jpg'
import antipro from '../img/accesorios/anti-propros.jpg'



const Accesorio = () => {
    return (
        <div className='accesorios' id='accesorios' >
            <Card 
                img={antipelota}
                name={'Antivibrador Pelota'}
                precio={'400 $'}
            />
            <Card 
                img={antihead}
                name={'Antivibrador Head'}
                precio={'700 $'}
            />
            <Card 
                img={antipro}
                name={'Antivibrador pro'}
                precio={'500 $'}
            />
            <Card 
                img={diadora}
                name={'Muñequeras Diadora'}
                precio={'1100 $'}
            />
            <Card 
                img={plomo}
                name={'Metro de Plomo'}
                precio={'1500 $'}
            />
            <Card 
                img={protectorpadel}
                name={'Protecor padel'}
                precio={'900 $'}
            />
        </div>
    )
}

export default Accesorio