import React from 'react';
import Loading from './components/Loading';
import './App.css';
import Components from './components/Components';

 //import Ball from './components/Ball';

function App() {
  const [loading, setLoading] = React.useState(false);

  React.useEffect(() => {
    setLoading(true);
    setTimeout(() => {
      setLoading(false);
    }, 3000)
  },[])

  return (
    <div className="App">

      { loading ? 

        <Loading />

        :
        
      <Components />
        
      }
      

    </div>
    
  );
  }


export default App;
