import React from 'react'
import './css/Service.css'
import hand from '../img/accesorios/hand.png'
import whatsApp from '../img/whap-logo.png'
import goglemap from '../img/goglemap.png'
import instagram from '../img/instagram.png'
import ReactWhatsapp from 'react-whatsapp'

const Service = () => {
    return (
        <div className="service">
            <div className='hola'><h1>Hola<img  className="img-hand" src={hand} alt="" /></h1></div>
            
            <div className="s-encordado">
                <div className="titulo-mano">Encordados y Balanceos</div>
                <div className="s-mano">
                    <h3>Mano de Obra: </h3>800 $
                    <h3>Balance +10gr: </h3>2600 $
                    <h3>Balance -10gr: </h3>1600 $
                </div> 
            </div>
            <div className="whatsApp">
                    <a  className="box-1" href="https://www.google.com/maps/place/50+Libras/@-34.5458297,-58.439231,17z/data=!3m1!4b1!4m5!3m4!1s0x95bcb5c0cec83827:0xd10e89fe4c241d55!8m2!3d-34.5458341!4d-58.4370423">
                            <img className="gogle-img" src={goglemap} alt="" />
                            <h5>Estamos aqui!!</h5>
                    </a>
                    <ReactWhatsapp className="box-2" number="+54 9 11 3239-5970" message='Hola quisiera consultarte algo?'>
                            <img className="whap-img" src={whatsApp} alt="" />
                            <h5 className="whap-text">Compras y Consultas.</h5>
                    </ReactWhatsapp>
                    <a className="box-2 insta" href="https://www.instagram.com/50libras/">
                            <img className="insta-img" src={instagram} alt="" />
                            <h5>Siguenos !!</h5>
                    </a>
            </div>

        </div>
    )
}

export default Service