import React from 'react'
import './css/Footer.css'
import Home from '@iconscout/react-unicons/icons/uil-whatsapp'
import Ball from '@iconscout/react-unicons/icons/uil-tennis-ball'
import Grip from '@iconscout/react-unicons/icons/uil-gold'
import Strings from '@iconscout/react-unicons/icons/uil-bullseye'
import {Link} from 'react-scroll'
import ReactWhatsapp from 'react-whatsapp'
import ReactTooltip from 'react-tooltip'

const Footer = () => {
    return (
        
        <div className='footer'>

            <Link className="f-icons-text" spy={true} to='cuerdas' smooth={true} >
                    <Strings className="f-icons" color="black" size="2rem"/>
                    <span>Cuerdas</span>
            </Link>


            <Link className="f-icons-text" spy={true} to='pelotas' smooth={true} >
                    <Ball className="f-icons" color="black" size="2rem" />
                    <span>Pelotas</span>
            </Link>

            <Link className="f-icons-text" spy={true} to='accesorios' smooth={true} >
                    <Grip className="f-icons" color="black" size="2rem" />
                    <span>Accesorios</span>
            </Link>
            
            
                <ReactWhatsapp number="+54 9 11 3239-5970" message='Hola quisiera ordenar algo!!' className="react-w">
                    <p data-tip="hello " place="top"></p>
                    <ReactTooltip />
                    <Home className="" color="black" size="2rem" />
                    <span className="text-contact" >Contacto</span>
                </ReactWhatsapp>
            




        </div>
    )
}

export default Footer