import React from 'react'
import './css/Navbar.css'
import logo from '../img/logo.jpg'
import {Link} from 'react-scroll'

const Navbar = () => {
    return (
        <div className='navbar'>
            <div className="n-left">
                <div className="n-logo">
                    <img  className='n-logo' src={logo} alt="" />
                </div>
                <div className="n-name">Tennis shop</div>
            </div>
        <div className="n-right">
            <div className="n-list">
                <ul style={{listStyleType: 'none'}}>

            <Link className="n-li" spy={true} to='pelotas' smooth={true} >
                <li>Pelotas</li>
            </Link>

            <Link className="n-li" spy={true} to='cuerdas' smooth={true} >
                <li>Cuerdas</li>
            </Link>

            <Link className="n-li" spy={true} to='overgrips' smooth={true} >
                <li>Overgrips</li>
            </Link>
            <Link className="n-li" spy={true} to='accesorios' smooth={true} >
                <li>Accesorios</li>
            </Link>

                </ul>
            </div>
        </div>



        </div>
    )
}

export default Navbar